"use client"

import { motion } from 'framer-motion'
import { Github, ExternalLink } from 'lucide-react'

export default function ProjectCard({ project }){
  return (
    <motion.article
      initial={{ opacity: 0, y: 6 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{ y: -6 }}
      className="card"
    >
      <div className="flex items-start justify-between">
        <div>
          <h3 className="text-lg font-semibold">{project.title}</h3>
          <p className="mt-2 text-sm text-neutral-400">{project.description}</p>
        </div>
        <div className="flex items-center gap-3">
          {project.repo && (
            <a href={project.repo} className="text-neutral-400 hover:text-neutral-200" aria-label="Repository">
              <Github />
            </a>
          )}
          {project.demo && (
            <a href={project.demo} className="text-neutral-400 hover:text-neutral-200" aria-label="Live demo">
              <ExternalLink />
            </a>
          )}
        </div>
      </div>

      <div className="mt-4 flex flex-wrap gap-2">
        {project.tags?.map((t) => (
          <span key={t} className="text-xs px-2 py-1 rounded bg-neutral-800 text-neutral-300">{t}</span>
        ))}
      </div>
    </motion.article>
  )
}
